.banner{
	color: white;
	object-fit: contain;
	height: 448px;
}
.banner__contents{
	    margin-left: 30px;
    padding-top: 93px;
    height: 190px;
}
.banner__title{
	margin-top: 60px;
	font-size: 3rem;
	font-weight: 800;
	padding-bottom: 0.3rem ;
} 
.banner__description{
	/* width: 45rem; */
	line-height: 1.3;
	padding-top: 1rem;
	font-size: 0.8rem;
	/* max-width: 36px; */
	height: 80px;
}
.banner__button{
	cursor: pointer;
	color: #fff;
	outline:none;
	border: none;
	font-weight: 700;
	border-radius: 0.2vw;
	/* padding-left: 2rem; */
	margin-right: 2rem;
	padding-top: 0.5rem;
	background-color: rgba(51,51,51,0.5);
	padding-bottom: 0.5rem;
	width: 80px;
}
.banner__button:hover{
	color: #000;
	background-color: #e6e6e6;
	transition: all 0.2s;
}
.banner--fadeBottom{
	height: 10.4rem;
	    background-image: linear-gradient(
180deg,transparent,rgba(37,37,37,0.61),#222);
}
