.row{
	margin: 20px;
	color: white;
}
.row__posters{
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	padding: 20px; 
}
.row__posters::-webkit-scrollbar{
	display: none;
}
.row__poster{
	object-fit: contain;
	width: 100%;
	max-height: 100px;
	margin-right: 10px;
	transition: transform 450ms;
}
.row__poster:hover{
	transform: scale(1.08);
	opacity: 1;
}
.row__posterLarge:hover{
	transform: scale(1.09);
	opacity: 1;
}
.row__posterLarge{
	margin-top: -30px;
	max-height: 250px ;
}